*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;}

body{
  background-color: antiquewhite;
}
header{
  width:auto;
  height: 7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 27px;
  text-shadow: 2px 2px #FF0000;

 background-color: rgba(255, 255, 255, .2);
 box-shadow: 0 5px 15px rgba(0, 0, 0, .5);
}
img{
height: 64px;
border-radius: 5px;
}
h2{
  text-align: center;
  margin-top: 11rem;
}
.CreateMain{
  height: 250px;
  width: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 17rem;
  margin-top: 5rem;
  /* background-color: #FF0000; */
  box-shadow:
       inset 0 -3em 3em rgba(0,0,0,0.1),
             0 0  0 2px rgb(255,255,255),
             0.3em 0.3em 1em rgba(0,0,0,0.3);
}
.item{
  width: 600px;
  height: 600px;
  border: none;
  outline: none;
  height: 50px;
  padding: 15px;
  
}
.item1{
  height: 5rem;
  font-size: 22px;
  font-weight: bold;
}
.item2{
  height: 10rem;
  font-size: 20px;
}
.fa{font-size: 30px;}
.NodeDive{
  /* background-color: lawngreen; */
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  align-items: start;}
.inner{
  border: 2px solid purple;
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: large;
  letter-spacing: 2px;
  padding: 20px;
  
}
.buuton{
  margin-top: 2rem;
  border: 2px groove rgb(195, 206, 185);
  margin-left: 1rem;
  background-color: transparent;
  border-radius: 2px;
  cursor: pointer;
  padding: 20px;
  padding-bottom: 40px;
  font-size: 20px;
}
.abs{
  display: flex;
  flex-wrap: wrap;

}













